body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.search-api-endpoint{
    border: none;
    background: none;
    margin-bottom: 20px;
}

.search-quickPick{
    margin: 0px 10px 10px 10px;
}

.form-control input{
    border-radius: 20px;
}


.imagelist{
    display: block;
    width: 70%;
    margin: auto;
    
}

.imagelist h3, .imagelist span{
    font-size: 12px;
}

.imagelist img{
    width: 150px;
    height: 350px;
    box-shadow: 1px 2px 1px gray;
}
.Films{
    display: 'block';
    width: 80%;
    margin: auto;
}

.Films img{
    margin-right: 10px;
    box-shadow: 1px 2px 1px grey;
}
